@import "../utils/variables";

.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;

    transition: color $c-change-during, transform $scale-change-during;

    &:hover {
      transform: scale($scale-size);
      color: $card-hov-text;
    }

    &:not(:nth-child(4n)) {
      margin-right: 48px;
    }
  }
}
